import { Button } from "antd";

const ThemeButton = ({ text, className, href,onClick}) => {
  return (
    <Button
      className={`${className} bg-gradient-to-l to-gradientTo from-gradientFrom text-white h-11 w-full hover:!text-white !border-none font-bold`}
      href={href} onClick={onClick}
    >
      {text}
    </Button>
  );
};

export default ThemeButton;
